//Light
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: local('Open Sans Light'),
        url('../../fonts/open-sans-v20-latin_cyrillic-300.woff2') format('woff2'),
        url('../../fonts/open-sans-v20-latin_cyrillic-300.woff') format('woff')
}

//Regular
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local('Open Sans Regular'),
        url('../../fonts/open-sans-v20-latin_cyrillic-regular.woff2') format('woff2'),
        url('../../fonts/open-sans-v20-latin_cyrillic-regular.woff') format('woff')
}

//Semi-bold
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local('Open Sans Semibold'),
        url('../../fonts/open-sans-v20-latin_cyrillic-600.woff2') format('woff2'),
        url('../../fonts/open-sans-v20-latin_cyrillic-600.woff') format('woff')
}

//Bold
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local('Open Sans Bold'),
        url('../../fonts/open-sans-v20-latin_cyrillic-700.woff2') format('woff2'),
        url('../../fonts/open-sans-v20-latin_cyrillic-700.woff') format('woff')
}
